import {TweenMax, Power4} from 'gsap'


// initial state
const state = {
  sidebarOpen: false,
  titles: 'press this to load sidebar',
  offset: 0
}

// getters
const getters = {
  sidebarOpen: state => state.sidebarOpen,
  titles: state => state.titles,
  offset: state => state.offset
}

// actions
const actions = {
  toggleSidebar ({ commit, state }) {
    commit('toggleSidebar', state)
  },
  init({commit}, element){
    commit('computeInitialisation',element);
  },
  showIt({commit}, element){
    commit('showIt',element);
  }
}

// mutations
const mutations = {
  toggleSidebar(state) {
    state.sidebarOpen = !state.sidebarOpen
  },
  computeInitialisation(state, element){
    TweenMax.set(element, {
      x: (-1) * element.offsetWidth
    })
    state.offset = (-1) * element.offsetWidth
  },
  showIt(state,obj){
    const dX = obj.isOpen ? 0 : (-1) * obj.element.offsetWidth

    TweenMax.to(obj.element, 0.1, {
      x: dX,
      ease: Power4.easeOut
    })
    state.offset = dX
  }
}

export const sidebar = {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}


