import * as fromService from '@/services'

const state = {}

const actions = {
    postKitchen({ dispatch, rootGetters }, kitchen) {
        const kitchenService = new fromService.KitchenService(rootGetters['account/token'])
        kitchenService.postKitchen(kitchen).then(
            data => {
                if (data) {
                    dispatch('alert/success', 'Kitchen successfully uploaded', { root: true })
                    dispatch('assetInteraction/setReloadMap', true, { root: true })
                }
            },
            error => {
                dispatch('alert/error', error.response.data.Message, { root: true })
                dispatch('assetInteraction/setReloadMap', true, { root: true })
            }
        )
    }
}
const mutations = {}

const getters = {
    kitchenState: state => state.status
}
export const kitchens = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
