import * as fromService from '@/services'

const state = {
    employeeList: [],
    selectedDesk: undefined,
    selectedEmployeeId: undefined,
    reloadMap: false
}

const actions = {
    postDeskEmployee({ dispatch, commit, rootGetters }, deskEmployee) {
        const placePeopleService = new fromService.PlacePeopleService(rootGetters['account/token'])
        placePeopleService.postDeskEmployee(deskEmployee.deskId, deskEmployee.employeeId).then(
            data => {
                if (data) {
                    dispatch('alert/success', 'Desk successfully assigned', { root: true })
                }
            },
            error => {
                dispatch('alert/error', error.response.data.Message, { root: true })
            }
        )
        commit('clearSelection')
        commit('setReloadMap', true)
    },
    getAllEmployees({ commit, rootGetters }) {
        const placePeopleService = new fromService.PlacePeopleService(rootGetters['account/token'])
        placePeopleService.getAllEmployees().then(employeeList => {
            commit('setEmployeeList', employeeList)
        })
    },
    setSelectedDesk({ commit }, newSelectedDesk) {
        commit('setSelectedDesk', newSelectedDesk)
    },
    setSelectedEmployeeId({ commit }, newSelectedEmployeeId) {
        commit('setSelectedEmployeeId', newSelectedEmployeeId)
    },
    clearSelection({ commit }) {
        commit('clearSelection')
    },
    setReloadMap({ commit }, newReloadMap) {
        commit('setReloadMap', newReloadMap)
    }
}

const mutations = {
    setEmployeeList: (state, employeeList) => {
        state.employeeList = employeeList
    },
    setSelectedDesk: (state, newSelectedDesk) => {
        state.selectedDesk = newSelectedDesk
    },
    setSelectedEmployeeId: (state, newSelectedEmployeeId) => {
        state.selectedEmployeeId = newSelectedEmployeeId
    },
    clearSelection: state => {
        state.selectedDesk = undefined
        state.selectedEmployeeId = undefined
    },
    setReloadMap: (state, newReloadMapState) => {
        state.reloadMap = newReloadMapState
    }
}

const getters = {
    placePeopleState: state => state.status
}

export const placePeople = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
