<template>
    <v-layout justify-center align-center="center" column fill-height>
        <div class="info">
            <v-layout>
                <v-flex xs12>
                    <v-list two-line>
                        <v-list-tile>
                            <v-list-tile-action>
                                <v-icon color="indigo">person</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>{{ userProfile.firstName }}</v-list-tile-title>
                                <v-list-tile-sub-title>First Name</v-list-tile-sub-title>
                            </v-list-tile-content>
                        </v-list-tile>
                        <v-divider inset></v-divider>

                        <v-list-tile>
                            <v-list-tile-action></v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>{{ userProfile.lastName }}</v-list-tile-title>
                                <v-list-tile-sub-title>Last Name</v-list-tile-sub-title>
                            </v-list-tile-content>
                        </v-list-tile>

                        <v-divider inset></v-divider>

                        <v-list-tile>
                            <v-list-tile-action>
                                <v-icon color="indigo">mail</v-icon>
                            </v-list-tile-action>

                            <v-list-tile-content>
                                <v-list-tile-title>{{ userProfile.email ? userProfile.email : '' }}</v-list-tile-title>
                                <v-list-tile-sub-title>Work</v-list-tile-sub-title>
                            </v-list-tile-content>
                        </v-list-tile>

                        <v-divider inset></v-divider>

                        <v-hover v-slot:default="{ hover }" open-delay="200">
                            <v-list-tile @click="editEmployeeLocation">
                                <v-list-tile-action>
                                    <v-icon color="indigo">location_on</v-icon>
                                </v-list-tile-action>

                                <v-list-tile-content>
                                    <v-list-tile-title>{{ buildingName }}</v-list-tile-title>
                                    <v-list-tile-sub-title>{{ floorName }}</v-list-tile-sub-title>
                                </v-list-tile-content>
                                <v-icon color="grey" v-show="hover">edit</v-icon>
                            </v-list-tile>
                        </v-hover>
                    </v-list>
                </v-flex>
            </v-layout>
            <v-layout style="background:#fff;">
                <v-flex xs12>
                    <v-btn style="float:right;" flat color="indigo" @click="goBack">Back to Home</v-btn>
                    <v-btn
                        style="float:right;"
                        flat
                        color="indigo"
                        @click="save"
                        :disabled="!dirty"
                    >Save</v-btn>
                </v-flex>
            </v-layout>
        </div>
        <div class="info mt-3">
            <FloorSelector @floorSelected="onFloorSelected" v-if="showFloorSelector" />
        </div>
    </v-layout>
</template>

<script>
import FloorSelector from '@/components/employee/FloorSelector.vue'
import { mapState, mapActions } from 'vuex'

export default {
    name: 'UserProfile',
    components: {
        FloorSelector
    },
    data: () => {
        return {
            dirty: false,
            showFloorSelector: false,
            floorId: '',
            floorName: '',
            buildingId: '',
            buildingName: ''
        }
    },
    computed: {
        ...mapState('account', ['status', 'user', 'userLocation']),
        ...mapState('account', { userProfile: 'profile' })
    },
    methods: {
        ...mapActions('account', ['getProfile']),
        ...mapActions('employee', ['updateEmployeeFloor']),
        ...mapActions('toolbar', ['showAdminToolbar', 'hideAdminToolbar']),
        editEmployeeLocation() {
            this.showFloorSelector = true
        },
        goBack() {
            this.$router.go(-1)
        },
        save() {
            this.updateEmployeeFloor({ floorId: this.floorId, employeeId: this.user.employeeId })
            this.dirty = false
        },
        onFloorSelected(floorInfo) {
            this.floorId = floorInfo.floorId
            this.floorName = floorInfo.floorName
            this.buildingName = floorInfo.buildingName
            this.dirty = true
            this.showFloorSelector = false
        }
    },
    watch: {},
    created() {
        this.hideAdminToolbar()
        this.getProfile()
        this.floorName = this.userLocation.floor
        this.buildingName = this.userLocation.building
    },
    beforeRouteLeave(to, from, next) {
        this.showAdminToolbar()
        next()
    }
}
</script>

<style lang="scss" scoped>
@import '@/styles/global.scss';

.reddish {
    background-color: $reddish !important;
}

.register-text {
    font-family: Roboto;
    font-size: 35px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #000000;
}

.info {
    width: 50%;
}

g-button {
    @include big-red-button;
}
</style>
