import axios from 'axios'
import { apiConfig } from '@/common/apiConfig'
import { Promise } from 'q'
import {authHeader} from '@/common/auth.header'

export const printerService = {
    postPrinter,
    getPrintersForFloor
}

function postPrinter(printer) {
    const axiosInstance = axios.create({});
    const data = JSON.stringify(printer)
    const url = `${apiConfig.apiUrl}/printers`
    const headers = { 'Content-Type': 'application/json', ...authHeader()}
    const method = 'POST'
    return axiosInstance({ method, url, data, headers }).then(handleResponse)
}

function getPrintersForFloor(floorId) {
    const axiosInstance = axios.create({});
    const data = JSON.stringify({
        tenantId: floorId
    })
    const url = `${apiConfig.apiUrl}/floors/${floorId}/printers`
    const headers = { ...authHeader()}
    const method = 'GET'
 
    return axiosInstance({ method, url, data, headers }).then(handleResponse)
 }

function handleResponse(response) {
    const data = response.data.data
    if (response.status !== 200) {
        const error = data.errors || response.statusText
        return Promise.reject(error)
    }

    return new Promise(resolve => {
        resolve(data)
    })
}