import * as fromService from '@/services'

const state = {
    meetingRoomDetails: null
}

const actions = {
    postMeetingRoom({ dispatch, rootGetters }, meetingRoom) {
        const meetingRoomsService = new fromService.MeetingRoomsService(rootGetters['account/token'])
        meetingRoomsService.postMeetingRoom(meetingRoom).then(
            data => {
                if (data) {
                    dispatch('assetInteraction/setReloadMap', true, { root: true })
                    dispatch('alert/success', 'Meeting room successfully uploaded', { root: true })
                }
            },
            error => {
                dispatch('assetInteraction/setReloadMap', true, { root: true })
                dispatch('alert/error', error.response.data.Message, { root: true })
            }
        )
    },
    getMeetingRoomDetails({ commit, rootGetters }, meetingRoomId) {
        const meetingRoomsService = new fromService.MeetingRoomsService(rootGetters['account/token'])
        if (meetingRoomId) {
            meetingRoomsService.getMeetingRoomDetails(meetingRoomId).then(meetingRoomDetails => {
                commit('setMeetingRoomDetails', meetingRoomDetails)
            })
        } else {
            commit('setMeetingRoomDetails', null)
        }
    }
}

const mutations = {
    setMeetingRoomDetails: (state, meetingRoomDetails) => {
        state.meetingRoomDetails = meetingRoomDetails
    }
}

const getters = {
    meetingRoomsState: state => state.status
}

export const meetingRooms = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
