import { printerService } from '../services/printer.service';

const state = {
}

const actions = {
    postPrinter({dispatch}, printer) {
        printerService.postPrinter(printer).then(
            data => {
                if(data) {
                    dispatch('alert/success', 'Printer successfully uploaded', { root: true })
                    dispatch('assetInteraction/setReloadMap', true, { root: true })
                }
            },
            error => {
                dispatch('alert/error', error.response.data.Message, { root: true })
                dispatch('assetInteraction/setReloadMap', true, { root: true })
            }
        )
    }
}
const mutations = {
}

const getters = {
    printerState: state => state.status
}
export const printers = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}