import UserService from '@/services/user.service'
import { roles } from '@/common/roles'
import { USER_PROFILE } from './account.actionTypes'
import * as fromService from '@/services'

const cachedUser = JSON.parse(localStorage.getItem('user-key'))
const cachedUserLocation = JSON.parse(localStorage.getItem('user-location-key'))
const sessionObject = JSON.parse(localStorage.getItem('session-key'))
const state = {
    status: {
        loggedIn: cachedUser ? true : false
    },
    user: {
        token: '',
        ...cachedUser
    },
    profile: {},
    userLocation: {
        ...cachedUserLocation
    },
    session: {
        floorId: '',
        ...sessionObject
    }
}

const actions = {
    loginGuest({ getters }) {
        const userService = UserService(getters.token)
        userService.guestLogin()
    },
    loginEmployee({ getters }) {
        const userService = UserService(getters.token)
        userService.employeeLogin()
    },
    login({ dispatch, commit, getters }, { token, router }) {
        if (!token) {
            commit('logout')
            dispatch('alert/error', 'Login error.', { root: true })
            return
        }

        commit('setToken', token)

        const userService = UserService(getters.token)

        return userService
            .login(token)
            .then(user => {
                if (!user) {
                    throw { message: 'Login error.' }
                }

                commit('loginSuccess', user)

                return userService.getUserLocation(user.employeeId).then(userLocation => {
                    commit('setLocation', userLocation)
                    localStorage.setItem('user-location-key', JSON.stringify(userLocation))
                    commit('setSessionFloorId', userLocation.floorId)

                    localStorage.setItem('session-key', JSON.stringify({ floorId: userLocation.floorId }))

                    if (user.roles.some(role => role == roles.guest)) {
                        return 'floor-map'
                    }

                    if (!userLocation.buildingId || !userLocation.floorId || !userLocation.locationId || !userLocation.tenantId) {
                        return 'where-am-i'
                    }

                    if (user.roles.some(role => role == roles.officeAdministrator)) {
                        return 'office-management'
                    }

                    if (user.roles.some(role => role == roles.employee)) {
                        return 'floor-map'
                    }
                })
            })
            .then(routeToPush => {
                dispatch('alert/success', 'Login successful', { root: true })
                router.push({ name: routeToPush })
            })
            .catch(error => {
                commit('logout')
                dispatch('alert/error', error.message, { root: true })
            })
    },

    changeFloor({ commit, getters, dispatch }, { floorId }) {
        if (!floorId || getters.selectedFloorId === floorId) {
            return
        }
        dispatch('mapInteractions/changeFloor', floorId, { root: true })
        commit('setSessionFloorId', floorId)
    },

    logout({ commit }) {
        commit('logout')
    },

    getProfile({ commit, getters }) {
        const userService = UserService(getters.token)

        userService.getProfile().then(profile => {
            commit(USER_PROFILE, profile)
        })
    },
    saveFloor({ commit, getters }, newFloorID) {
        const floorService = new fromService.FloorService(getters.token)
        floorService.getLocationInformationByFloodId(newFloorID).then(locationInformation => {
            commit('setLocation', locationInformation)
        })
    },
    setLocation({ commit }, newLocation) {
        commit('setLocation', newLocation)
    }
}

const mutations = {
    setToken(state, token) {
        state.user.token = token
        localStorage.setItem('user-key', JSON.stringify(state.user))
    },
    loginSuccess(state, user) {
        state.status.loggedIn = true

        state.user = {
            ...user,
            token: state.user.token
        }

        localStorage.setItem('user-key', JSON.stringify(state.user))
    },
    logout(state) {
        state.status.loggedIn = false
        state.user = {
            token: ''
        }

        state.session.floorId = ''
        localStorage.removeItem('user-key')
        localStorage.removeItem('session-key')
        localStorage.removeItem('user-location-key')
    },

    [USER_PROFILE](state, profile) {
        state.profile = { ...profile }
    },
    setLocation(state, userLocation) {
        state.userLocation = { ...userLocation }
    },
    setSessionFloorId(state, floorId) {
        state.session.floorId = floorId
    }
}

const getters = {
    userState: state => state.status,
    user: state => state.user,
    token: state => state.user.token,
    employeeId: state => state.user.employeeId,
    userLocation: state => state.userLocation,
    selectedFloorId: state => state.session.floorId,
    isLoggedIn: state => state.status.loggedIn && state.user && state.user.token && state.user.roles && state.user.roles.length > 0
}

export const account = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
