<template>
  <v-app>
    <v-content fluid>
      <router-view></router-view>
    </v-content>
    <v-snackbar v-model="snackbar" :right="true" :timeout="timeout" :top="true" :color="color">
      {{ alert.message }}
      <v-btn color="#fff" flat @click="snackbar = false">Close</v-btn>
    </v-snackbar>
  </v-app>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex'
import { ALERT_SHOW_ALERT, ALERT_CLEAR, ALERT } from '@/store/alert.actionTypes'

export default {
    name: 'App',

    data: () => ({
        timeout: 2000,
        message: null,
        color: 'success',
        snackbar: false
    }),
    computed: {
        ...mapState({
            alert: state => state.alert
        }),
        ...mapGetters(ALERT, [ALERT_SHOW_ALERT]),
        ...mapGetters('account', ['isLoggedIn'])
    },
    methods: {
        ...mapActions(ALERT, {
            clearAlert: ALERT_CLEAR
        })
    },
    watch: {
        $route() {
            this.clearAlert()
        },
        showAlert() {
            if (this.alert.type === 'alert-error') {
                this.color = 'error'
                this.timeout = 0
            }

            if (this.alert.type === 'alert-success') {
                this.color = 'success'
                this.timeout = 5000
            }

            this.snackbar = this.showAlert
        },
        isLoggedIn(val) {
            if (!val) {
                this.$router.push('/')
            }
        }
    }
}
</script>

