import * as fromService from '@/services'

const state = {}

const actions = {
    postBathroom({ dispatch, rootGetters }, bathroom) {
        const bathroomService = new fromService.BathroomService(rootGetters['account/token'])
        bathroomService.postBathroom(bathroom).then(
            data => {
                if (data) {
                    dispatch('alert/success', 'Bathroom successfully uploaded', { root: true })
                    dispatch('assetInteraction/setReloadMap', true, { root: true })
                }
            },
            error => {
                dispatch('alert/error', error.response.data.Message, { root: true })
                dispatch('assetInteraction/setReloadMap', true, { root: true })
            }
        )
    }
}
const mutations = {}

const getters = {
    bathroomState: state => state.status
}
export const bathrooms = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
