import { DEFAULT } from '@/store/commonSpaces.entityTypes.js'

const state = {
    mapStates: { viewing: true, drawing: false, adding: false, editing: false },
    assetSelected: DEFAULT,
    drawnShape: undefined,
    reloadMap: false,
    defaultMapState: {
        drawing: false,
        viewing: false,
        editing: false,
        adding: false
    }
}

const actions = {
    setAssetSelected({ commit }, assetSelected) {
        commit('setAssetSelected', assetSelected)
    },
    setDrawnShape({ commit }, drawnShape) {
        commit('setDrawnShape', drawnShape)
    },
    setReloadMap({ commit }, reloadMap) {
        commit('setReloadMap', reloadMap)
    }
}

const mutations = {
    setViewingMode: state => {
        state.mapStates = {
            ...state.defaultMapState,
            viewing: true
        }
    },
    setDrawingMode: state => {
        state.mapStates = {
            ...state.defaultMapState,
            drawing: true
        }
    },
    setAddingMode: state => {
        state.mapStates = {
            ...state.defaultMapState,
            adding: true
        }
    },
    setEditingMode: state => {
        state.mapStates = {
            ...state.defaultMapState,
            editing: true
        }
    },
    setAssetSelected: (state, assetSelected) => {
        state.assetSelected = assetSelected
    },
    setDrawnShape: (state, drawnShape) => {
        state.drawnShape = drawnShape
    },
    setReloadMap: (state, reloadMap) => {
        state.reloadMap = reloadMap
    }
}

const getters = {
    drawState: state => state.status //maybe state.mapStates.drawing ?
}

export const assetInteraction = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
