import Vue from 'vue'
import Router from 'vue-router'

import FloorSelector from '@/components/employee/FloorSelector'
import EmployeeAssets from './components/employee/EmployeeAssets'
import UserProfile from './components/content/UserProfile'
import NotFound from '@/views/NotFound'
import NProgress from 'nprogress'
import store from '@/store'
import { roles } from '@/common/roles'

Vue.use(Router)

const isLoggedIn = () => {
    const user = store.getters['account/user']
    const userState = store.getters['account/userState']

    return userState && userState.loggedIn && user && user.roles && user.roles.length
}

const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'landing-page',
            component: () => import(/* webpackChunkName: "home" */ './views/accounts/LandingPage.vue'),
            beforeEnter: (to, from, next) => {
                if (!isLoggedIn()) {
                    return next()
                }

                const user = store.getters['account/user']

                if (user.roles.some((role) => role == roles.officeAdministrator)) {
                    return next({ name: 'office-management' })
                }

                if (user.roles.some((role) => role == roles.employee || role == roles.guest)) {
                    return next({ name: 'floor-map' })
                }
            },
        },
        {
            path: '/where-am-i',
            name: 'where-am-i',
            component: () => import(/* webpackChunkName: "home" */ './views/accounts/LocatorPage.vue'),
        },
        {
            path: '/floor-map',
            name: 'floor-map',
            component: () => import(/* webpackChunkName: "register" */ './views/map/EmployeeView.vue'),
            redirect: '/floor-map/employee-assets',
            children: [
                {
                    path: 'employee-assets',
                    name: 'employee-assets',
                    component: EmployeeAssets,
                },
                {
                    path: 'myprofile',
                    name: 'myprofile',
                    component: UserProfile,
                    children: [
                        {
                            path: 'employee-location',
                            name: 'employee-location-admin',
                            component: FloorSelector,
                        },
                    ],
                },
            ],
            props: true,
            beforeEnter(routeTo, routeFrom, next) {
                routeTo.params.status = store.getters['account/userState']
                next()
            },
        },
        {
            path: '/office-management',
            name: 'office-management',
            component: () => import(/* webpackChunkName: "admin"*/ './views/office-management/OfficeManagementPage.vue'),
            redirect: '/office-management/plans',
            children: [
                {
                    path: 'plans',
                    name: 'plans',
                    component: () => import(/* webpackChunkName: "floors" */ './views/office-management/plans-management/UploadPlansPage.vue'),
                    children: [
                        {
                            path: 'add-floor-plan',
                            name: 'add-floor-plan',
                            props: true,
                            component: () => import(/* webpackChunkName: "floors" */ './components/content/UploadFirstPlanForm.vue'),
                        },
                        {
                            path: '',
                            name: 'upload-plan',
                            component: () => import(/* webpackChunkName: "floors" */ './components/content/PlanDashboard'),
                        },
                    ],
                },
                {
                    path: 'create-building',
                    name: 'create-building',
                    component: () => import(/* webpackChunkName: "buildings" */ './views/office-management/settings/BuildingSetup.vue'),
                },
                {
                    path: 'buildings',
                    name: 'buildings',
                    component: () => import(/* webpackChunkName: "buildings" */ './views/office-management/dashboards/BuildingsDashboard.vue'),
                },
                {
                    path: 'hazards-dashboard',
                    name: 'hazards-deshboard',
                    component: () => import(/* webpackChunkName: "Hazards" */ './views/office-management/dashboards/HazardsDashboard.vue')
                }, 
                {
                    path: 'notifications',
                    name: 'notifications',
                    component: () => import(/* webpackChunkName: "Notifications" */ './views/office-management/notifications/Mailbox.vue')
                }, 
                {
                    path: 'calendar',
                    name: 'calendar',
                    component: () => import(/* webpackChunkName: "Calendar" */ './views/office-management/calendar/Calendar.vue')
                },                                                
                {
                    path: 'assets',
                    name: 'assets',
                    props: true,
                    beforeEnter: (to, from, next) => {
                        const floorId = store.getters['account/selectedFloorId']
                        store.dispatch('floorLayout/getLayout', floorId).then((mapLayout) => {
                            to.params.floorMap = mapLayout.floorLayoutImage
                            to.params.floorId = floorId
                            next()
                        })
                    },
                    component: () => import(/* webpackChunkName: "assets" */ './views/office-management/assets-management/PlaceAssetsPage.vue'),
                },
                {
                    path: 'employees',
                    name: 'employees',
                    props: true,
                    beforeEnter: (to, from, next) => {
                        const userLocation = store.getters['account/userLocation']
                        to.params.logedUserLocation = userLocation
                        next()
                    },
                    component: () => import(/* webpackChunkName: "people" */ './views/office-management/employees/EmployeesSearchPage.vue'),
                },
                {
                    path: 'people',
                    name: 'people',
                    props: true,
                    beforeEnter: (to, from, next) => {
                        const floorId = store.getters['account/selectedFloorId']
                        store.dispatch('floorLayout/getLayout', floorId).then(() => {
                            to.params.floorId = floorId
                            next()
                        })
                    },
                    component: () => import(/* webpackChunkName: "people" */ './views/office-management/people/PlacePeoplePage.vue'),
                },
                {
                    path: 'myprofile',
                    name: 'myprofile',
                    component: UserProfile,
                    children: [
                        {
                            path: 'employee-location',
                            name: 'employee-location-admin',
                            component: FloorSelector,
                        },
                    ],
                },
            ],
        },
        {
            path: '/login-redirect',
            beforeEnter: (to, from, next) => {
                const token = to.query['tkn']
                store.dispatch('account/login', { token, router })
                next()
            },
        },
        { path: '/404', name: 'notFound', component: NotFound },
        { path: '*', redirect: '/404' },
        {
            path: '/office-management/notifications',
            name: 'notifications',
            component: () => import('./views/office-management/notifications/Mailbox.vue'),
        }
    ],
})

router.beforeEach((to, from, next) => {
    NProgress.start()
    const publicPages = ['/login-redirect', '/']
    const authRequired = !publicPages.includes(to.path)

    if (authRequired && !isLoggedIn()) {
        return next('/')
    }

    next()
})

router.afterEach(() => {
    NProgress.done()
})

export default router
