import clientFactory from '@/common/apiClientFactory'
import { apiConfig } from '@/common/apiConfig'

export default function UserService(token) {
    const apiClient = clientFactory.apiClient(token)
    const accountsClient = clientFactory.accountsClient(token)

    function guestLogin() {
        const returnUrl = new URL(`${window.location.protocol}${window.location.host}`)
        returnUrl.pathname = 'login-redirect'

        window.location.href = `${apiConfig.accountsUrl}/guestLogin?returnUrl=${encodeURIComponent(returnUrl)}`
    }

    function employeeLogin() {
        const returnUrl = new URL(`${window.location.protocol}${window.location.host}`)
        returnUrl.pathname = 'login-redirect'

        window.location.href = `${apiConfig.accountsUrl}/employeeLogin?returnUrl=${encodeURIComponent(returnUrl)}`
    }

    function login() {
        return accountsClient.get('/user-details')
    }

    function getProfile() {
        return accountsClient.get('/profile')
    }

    function getUserLocation(employeeId) {
        return apiClient.get(`/employees/${employeeId}/location`)
    }

    return {
        guestLogin,
        employeeLogin,
        login,
        getProfile,
        getUserLocation
    }
}
