import axios from 'axios'
import { apiConfig } from '@/common/apiConfig'
import { Promise } from 'q'
import { authHeader } from '@/common/auth.header'

export const searchService = {
    search,
    getSearchResultLocation
}

function search(searchString) {
    const axiosInstance = axios.create({})

    const data = JSON.stringify({
        search: searchString
    })

    const url = `${apiConfig.apiUrl}/search/${searchString}`
    const headers = { ...authHeader() }
    const method = 'GET'

    return axiosInstance({ method, url, data, headers })
    .then(handleResponse)
}

function getSearchResultLocation(searchedItemId) {
    const axiosInstance = axios.create({})
    const url = `${apiConfig.apiUrl}/Search/${searchedItemId}/location`
    const headers = { 'Content-Type': 'application/json', ...authHeader()}
    const method = 'GET'
    return axiosInstance({ method, url, headers }).then(handleResponse)
}

 function handleResponse(response) {
    const data = response.data.data
    if (response.status !== 200) {
        const error = data.errors || response.statusText
        return Promise.reject(error)
    }
    return new Promise(resolve => {
        resolve(data)
    })
}