import BaseService from './base-service'

export class DeskService extends BaseService {
    constructor(token) {
        super(token)
    }

    postDesk(desk) {
        return this.apiClient.post('/desks', JSON.stringify(desk))
    }

    getDesksForFloor(floorId) {
        return this.apiClient.get(`/floors/${floorId}/desks`)
    }
}
