<template>
    <v-card class="add-hazard-container">
        <v-form ref="form" v-model="valid">
            <v-container fluid>
                <v-layout justify-start column fill-height>
                    <v-flex xs1>
                        <h1 class="title">Report hazard</h1>
                    </v-flex>
                    <template v-if="!hazardPosition">
                        <v-flex xs1>
                            <p
                                class="first-step-text"
                            >In order to report a new hazard, first specify its position by clicking on the floor map!</p>
                        </v-flex>
                        <v-flex xs1 class="center">
                            <v-btn flat color="primary" @click="setReportHazard(false)">Cancel</v-btn>
                        </v-flex>
                    </template>
                    <template v-else>
                        <v-flex xs1>
                            <v-text-field label="Name" v-model="hazard.name" :rules="nameRules"></v-text-field>
                        </v-flex>
                        <v-flex xs1>
                            <v-select
                                :items="categories"
                                label="Category"
                                v-model="hazard.category"
                                :rules="categoryRules"
                            ></v-select>
                        </v-flex>
                        <v-flex xs1>
                            <v-textarea
                                label="Description"
                                v-model="hazard.description"
                                :rules="descriptionRules"
                            ></v-textarea>
                        </v-flex>

                        <v-flex xs1>
                            <v-layout>
                                <v-dialog v-model="dialog" persistent max-width="290">
                                    <template v-slot:activator="{ on }">
                                        <v-btn color="primary" flat dark v-on="on">Cancel</v-btn>
                                    </template>
                                    <v-card>
                                        <v-card-title class="headline">Dismiss changes?</v-card-title>
                                        <v-card-text>If you dismiss changes you will lose all the data that was not saved.</v-card-text>
                                        <v-card-actions>
                                            <v-layout justify-end>
                                                <v-btn
                                                    flat
                                                    @click="dialog = false"
                                                    color="primary"
                                                >No</v-btn>
                                                <v-btn
                                                    color="primary"
                                                    flat
                                                    text
                                                    @click="dismissChanges"
                                                >Dismiss Changes</v-btn>
                                            </v-layout>
                                        </v-card-actions>
                                    </v-card>
                                </v-dialog>
                                <v-btn :disabled="!valid" color="primary" @click="save">Save</v-btn>
                            </v-layout>
                        </v-flex>
                    </template>
                </v-layout>
            </v-container>
        </v-form>
    </v-card>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
    data: () => ({
        dialog: false,
        valid: true,
        categories: ['Missing supply', 'Danger', 'Cleaning'],
        hazard: {
            name: undefined,
            description: undefined,
            category: undefined,
            floorId: undefined,
            shape: undefined
        },
        nameRules: [
            v => !!v || 'Name is required',
            v => (v && v.length >= 3) || 'Name must be more than 3 characters',
            v => (v && v.length <= 100) || 'Name must be less than 100 characters'
        ],
        descriptionRules: [
            v => !!v || 'Description is required',
            v => (v && v.length >= 3) || 'Description must be more than 3 characters',
            v => (v && v.length <= 500) || 'Description must be less than 500 characters'
        ],
        categoryRules: [v => !!v || 'Category is required']
    }),
    watch: {
        reportHazard() {
            if (!this.reportHazard) {
                this.clearHazard()
            }
        }
    },
    methods: {
        ...mapActions('employeeMap', ['setReloadAssets', 'postHazard', 'setReportHazard', 'setHazardPosition']),
        save() {
            if (this.$refs.form.validate()) {
                this.hazard.shape = this.hazardPosition
                this.hazard.floorId = this.floorId
                this.postHazard(this.hazard)
            }
        },
        dismissChanges() {
            this.dialog = false
            this.setReportHazard(false)
        },
        clearHazard() {
            this.dialog = false
            this.$refs.form.reset()
            this.hazard.floorId = undefined
            this.hazard.shape = undefined
            this.setHazardPosition(null)
        }
    },
    computed: {
        ...mapState('employeeMap', ['hazardPosition', 'reportHazard'])
    },
    props: {
        floorId: { required: true }
    }
}
</script>

<style lang="scss" scoped>
@import 'src/stylesheets/1-setttings/settings.colors.scss';

.title {
    text-align: center;
    color: $orange;
}

.add-hazard-container {
    height: 100%;
    min-width: 250px;
    max-width: 250px;
    background-color: white;
}

.first-step-text {
    // text-align: center;
    margin: 40px 10px 20px 10px;
}
</style>
