import * as fromService from '@/services'

const state = {
    imageContent: ''
}
const actions = {
    uploadLayout({ dispatch, rootGetters }, { imageInformation, router }) {
        var floorLayoutService = new fromService.FloorLayoutService(rootGetters['account/token'])
        floorLayoutService.uploadLayout(imageInformation).then(
            data => {
                if (data) {
                    dispatch('alert/success', 'Layout successfully uploaded', { root: true })
                    router.push('/office-management/plans')
                }
            },
            error => {
                dispatch('alert/error', error.response.data.errors[0], { root: true })
            }
        )
    },
    getLayout({ commit, rootGetters }, floorId) {
        var floorLayoutService = new fromService.FloorLayoutService(rootGetters['account/token'])
        return floorLayoutService.getLayout(floorId).then(imageContent => {
            commit('setLayout', imageContent)
            return imageContent
        })
    }
}
const mutations = {
    setLayout: (state, imageContent) => {
        state.imageContent = imageContent.floorLayoutImage
    }
}
const getters = {
    floorLayoutState: state => state.status
}
export const floorLayout = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
