<template>
    <v-layout class="container-fluid">
        <employeeMap></employeeMap>

        <div class="options-container">
            <div class="map-buttons">
                <v-btn
                    class="layer-button"
                    fab
                    dark
                    @click="toggleDisplay('layerSelector', 'hazardSelector','layer-button', 'hazard-button')"
                >
                    <svgicon icon="layers" width="30" height="30" color="#fff" padding="10px"></svgicon>
                </v-btn>
                <v-btn
                    class="hazard-button"
                    fab
                    dark
                    @click="toggleDisplay('hazardSelector', 'layerSelector', 'hazard-button', 'layer-button'); hazardButtonClicked();"
                >
                    <svgicon icon="hazards" width="30" height="30" color="#fff"></svgicon>
                </v-btn>
            </div>

            <div>
                <layerSelector class="layerSelector"></layerSelector>
                <addHazard class="hazardSelector" v-bind:floorId="selectedFloorId"></addHazard>
            </div>
        </div>
    </v-layout>
</template>

<script>
import '@/compiled-icons/hazards'
import '@/compiled-icons/layers'
import LayerSelector from '../../components/employee/LayerSelector.vue'
import EmployeeMap from '../../components/employee/EmployeeMap.vue'
import AddHazard from '@/components/employee/AddHazard.vue'
import { mapState, mapActions, mapGetters } from 'vuex'

export default {
    components: {
        employeeMap: EmployeeMap,
        layerSelector: LayerSelector,
        addHazard: AddHazard
    },
    data: () => ({
        lastActiveLayer: undefined
    }),
    computed: {
        ...mapState('employeeMap', ['reportHazard']),
        ...mapGetters('account', ['selectedFloorId'])
    },
    methods: {
        ...mapActions('employeeMap', ['setReportHazard']),
        toggleDisplay(option_active, option_inactive, button_active, button_inactive) {
            //save last clicked layer
            this.lastActiveLayer = option_active

            //change layer display
            if (document.getElementsByClassName(option_active)[0].style.display == 'flex') {
                this.closeAllLayers()
            } else {
                this.switchLayer(option_active, option_inactive, button_active, button_inactive)
            }
            //call that function
            if (this.lastActiveLayer === 'hazardSelector') {
                this.setReportHazard(true)
            }
            if (this.lastActiveLayer === 'layerSelector') {
                this.setReportHazard(false)
            }
        },
        closeAllLayers() {
            // hide both layers
            document.getElementsByClassName('hazardSelector')[0].style.display = 'none'
            document.getElementsByClassName('layerSelector')[0].style.display = 'none'

            //reset the buttons color
            document.getElementsByClassName('hazard-button')[0].style.opacity = 0.5
            document.getElementsByClassName('layer-button')[0].style.opacity = 0.5
        },
        switchLayer(option_active, option_inactive, button_active, button_inactive) {
            //hide inactive layer and show the active one
            document.getElementsByClassName(option_inactive)[0].style.display = 'none'
            document.getElementsByClassName(option_active)[0].style.display = 'flex'

            //switch the selected buttons
            document.getElementsByClassName(button_inactive)[0].style.opacity = 0.5
            document.getElementsByClassName(button_active)[0].style.opacity = 1
        },
        hazardButtonClicked() {
            this.setReportHazard(true)
        }
    },
    watch: {
        reportHazard() {
            if (!this.reportHazard && this.lastActiveLayer !== 'layerSelector') {
                this.closeAllLayers()
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import 'src/stylesheets/1-setttings/settings.colors.scss';

.container {
    max-width: 100vh;
}

.options-container {
    position: fixed;
    right: 0;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.map-buttons {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}

.layer-button {
    opacity: 0.5;
    background-color: $orange !important;
}

.hazard-button {
    opacity: 0.5;
    background-color: $orange !important;
}

.layerSelector {
    display: none;
    min-width: 250px;
}

.hazardSelector {
    display: none;
    min-width: 250px;
}
</style>
