import * as fromService from '@/services'

const state = {
    layerSelected: ['Meeting room', 'Kitchen', 'Bathroom', 'Printer', 'Recreation room', 'Reception', 'Exit', 'Desk', 'Hazard'],
    allLayers: ['Meeting room', 'Kitchen', 'Bathroom', 'Printer', 'Recreation room', 'Reception', 'Exit', 'Desk', 'Hazard'],
    selectAllLayers: true,
    reloadAssets: false,
    reportHazard: false,
    hazardPosition: undefined
}

const actions = {
    setLayerSelected({ commit }, newLayerSelected) {
        commit('setLayerSelected', newLayerSelected)
    },
    setSelectAllLayers({ commit }, newSelectAllLayers) {
        commit('setSelectAllLayers', newSelectAllLayers)
    },
    setReloadAssets({ commit }, newReloadAssets) {
        commit('setReloadAssets', newReloadAssets)
    },
    setReportHazard({ commit }, reportHazardValue) {
        commit('setReportHazard', reportHazardValue)
    },
    setHazardPosition({ commit }, hazardPosition) {
        commit('setHazardPosition', hazardPosition)
    },
    postHazard({ dispatch, rootGetters }, hazard) {
        const hazardService = new fromService.HazardService(rootGetters['account/token'])
        hazardService.postHazard(hazard).then(
            data => {
                if (data) {
                    dispatch('alert/success', 'Hazard successfully reported', { root: true })
                    dispatch('employeeMap/setReloadAssets', true, { root: true })
                }
            },
            error => {
                dispatch('alert/error', error.response.data.Message, { root: true })
            }
        )
    }
}

const mutations = {
    setLayerSelected: (state, newLayerSelected) => {
        state.layerSelected = newLayerSelected
    },
    setSelectAllLayers: (state, newSelectAllLayers) => {
        state.selectAllLayers = newSelectAllLayers
        if (state.selectAllLayers) {
            state.layerSelected = state.allLayers
        } else {
            state.layerSelected = []
        }
    },
    setReloadAssets: (state, newReloadAssets) => {
        if (newReloadAssets) {
            state.hazardPosition = undefined
            state.reportHazard = false
        }
        state.reloadAssets = newReloadAssets
    },
    setReportHazard: (state, hazardReportValue) => {
        state.reportHazard = hazardReportValue
    },
    setHazardPosition: (state, hazardPosition) => {
        state.hazardPosition = hazardPosition
    }
}

const getters = {
    employeeMapState: state => state.status
}
export const employeeMap = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
