import * as fromService from '@/services'
const state = {
    listOfEmployees: [],
    employeeInfo: [],
    profile: {
        floorId: '',
        employeeId: ''
    }
}

const actions = {
    getEmployeesByFloors({ commit, rootGetters }, floors) {
        var employeeService = new fromService.EmployeeService(rootGetters['account/token'])
        employeeService.getEmployees(floors).then(employes => {
            commit('setEmployees', employes)
        })
    },

    getAllEmployeeInfo({ commit, rootGetters }, username) {
        var employeeService = new fromService.EmployeeService(rootGetters['account/token'])
        return employeeService.getAllEmployeeInfo(username).then(info => {
            commit('setEmployeeInfo', info)
            return info
        })
    },
    clearEmployees({ commit }) {
        commit('setEmployees', [])
    },
    updateEmployeeFloor({ commit, dispatch, rootGetters }, { floorId, employeeId }) {
        var employeeService = new fromService.EmployeeService(rootGetters['account/token'])
        return employeeService
            .updateEmployeeFloor({ floorId, employeeId })
            .then(employeeLocation => {
                dispatch('account/setLocation', employeeLocation, { root: true })
                commit('setFloor', { floorId, employeeId })
                dispatch('alert/success', 'Employee floor saved succesfully.', { root: true })
            })
            .catch(error => {
                dispatch('alert/error', error.message, { root: true })
            })
    }
}

const mutations = {
    setFloor: (state, { floorId, employeeId }) => {
        state.profile = {
            ...state.profile,
            floorId,
            employeeId
        }
    },
    setEmployees: (state, listOfEmployees) => {
        state.listOfEmployees = listOfEmployees
    },
    setEmployeeInfo: (state, info) => {
        state.employeeInfo = info
    }
}

const getters = {}

export const employee = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
