import { ALERT_SUCCESS, ALERT_ERROR, ALERT_CLEAR, ALERT_SHOW_ALERT } from './alert.actionTypes'

const state = {
    type: null,
    message: null
}

const actions = {
    [ALERT_SUCCESS]({ commit }, message) {
        commit(ALERT_SUCCESS, message)
    },
    [ALERT_ERROR]({ commit }, message) {
        commit(ALERT_ERROR, message)
    },
    [ALERT_CLEAR]({ commit }) {
        commit(ALERT_CLEAR)
    }
}

const mutations = {
    [ALERT_SUCCESS](state, message) {
        state.type = 'alert-success'
        state.message = message
    },
    [ALERT_ERROR](state, message) {
        state.type = 'alert-error'
        state.message = message
    },
    [ALERT_CLEAR](state) {
        state.type = null
        state.message = null
    }
}

const getters = {
    [ALERT_SHOW_ALERT]: state => {
        return !!state.message
    }
}

export const alert = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
