<template>
    <v-container fill-height class="floor-selector">
        <v-layout column>
            <v-flex xs1>
                <v-select
                    :items="listOfTenants"
                    label="Country"
                    item-text="country"
                    item-value="tenantId"
                    solo
                    background-color="grey lighten-3"
                    :value="userLocation.tenantId"
                    @input="countrySelected"
                ></v-select>
            </v-flex>
            <v-flex xs1>
                <v-select
                    :items="listOfCities"
                    label="City"
                    item-text="name"
                    item-value="locationId"
                    solo
                    background-color="grey lighten-3"
                    :value="userLocation.locationId"
                    @input="getBuildingsWithFloors"
                ></v-select>
            </v-flex>
            <v-treeview
                :items="listOfBuildingsWithFloors"
                item-key="id"
                item-text="label"
                :open="[userLocation.buildingId]"
                :active="[userLocation.floorId]"
                dense
                activatable
                open-on-click
                @update:active="selectAsset"
            >
            </v-treeview>
        </v-layout>
    </v-container>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
    data: () => ({
        open: undefined,
        active: [],
        floorId: '',
        emitTreeSelection: false
    }),
    computed: {
        ...mapState('buildingInformation', ['listOfTenants', 'listOfCities', 'listOfBuildingsWithFloors']),
        ...mapState('account', ['userLocation'])
    },
    watch: {
        active() {
            this.selectAsset()
        }
    },
    methods: {
        ...mapActions('buildingInformation', ['getTenants', 'getCities', 'getBuildingsWithFloors']),
        ...mapActions('mapInteractions', ['changeFloor']),
        ...mapActions('account', ['saveFloor']),
        ...mapMutations('buildingInformation', ['setBuildingsWithFloors']),
        selectAsset: function(newValue) {
            if (this.emitTreeSelection === true && newValue && newValue.length === 1) {
                for (const building of this.listOfBuildingsWithFloors) {
                    const floor = building.children.find(floor => floor.id === newValue[0])
                    if (floor) {
                        this.floorId = newValue[0]
                        this.$emit('floorSelected', {
                            floorId: floor.id,
                            floorName: floor.label,
                            buldingId: building.id,
                            buildingName: building.label
                        })
                    }
                }
            }
            if (newValue.length > 0) {
                this.emitTreeSelection = true
            }
        },
        countrySelected(newValue) {
            this.citySelected = undefined
            this.setBuildingsWithFloors(undefined)
            this.getCities(newValue)
        }
    },
    created() {
        this.getTenants()
        if (this.userLocation.tenantId) {
            this.countrySelected(this.userLocation.tenantId)
            if (this.userLocation.locationId) {
                this.getBuildingsWithFloors(this.userLocation.locationId)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/styles/global.scss';
.stay-to-right {
    position: absolute;
    right: 25px;
}

.stay-to-bottom {
    position: absolute;
    bottom: 0px;
}

.no-padding {
    padding: 0px;
}

.default-padding {
    padding: 20px;
}

.default-margin {
    margin: 25px;
}

.card-outter {
    position: relative;
    padding-bottom: 50px;
}
.card-actions {
    position: absolute;
    bottom: 0;
}

.btn-save-Rectangle {
    @include red-button;
    width: 435px;
    height: 40px;
    margin-left: -3px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.floor-selector {
    background-color: $primary-color-4;
}
</style>
