import BaseService from './base-service'

export class FloorService extends BaseService {
    constructor(token) {
        super(token)
    }

    addFloor(name, type, building, level) {
        const data = {
            username: name,
            password: type,
            building: building,
            level: level
        }
        return this.apiClient.post('/floors', JSON.stringify(data))
    }

    getLocationInformationByFloodId(floorId) {
        return this.apiClient.get(`/floors/LocationByFloorId/${floorId}`)
    }
}
