import { searchService } from '../services/search.service';

const state = {
}

const actions = {
    searchAll({dispatch}, searchString) {
        searchService.search(searchString).then(
            data => {
                        if(data) {
                        return data;
                    }
            },
            error => {
                dispatch('alert/error', error.response.data.Message, { root: true })
            }
        )
    }
}
const mutations = {
}

const getters = {
    searchState: state => state.status
}

export const search = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}