import BaseService from './base-service'
export class MeetingRoomsService extends BaseService {
    constructor(token) {
        super(token)
    }

    postMeetingRoom(meetingRoom) {
        return this.apiClient.post('/meeting-rooms', JSON.stringify(meetingRoom))
    }
    getMeetingRoomsForFloor(floorId) {
        return this.apiClient.get(`/floors/${floorId}/meeting-rooms`)
    }

    getMeetingRoomDetails(meetingRoomId) {
        return this.apiClient.get(`/meeting-rooms/${meetingRoomId}/details`)
    }
}
