import * as fromService from '@/services'

const state = {
    listOfTenants: [],
    listOfCities: [],
    listOfBuildings: [],
    listOfBuildingsWithFloors: [],
    listOfFloors: []
}

const actions = {
    getTenants({ commit, rootGetters }) {
        const buildingInformationService = new fromService.BuildingInformationService(rootGetters['account/token'])
        buildingInformationService.getTenants().then(listOfTenants => {
            commit('setTenants', listOfTenants)
        })
    },

    getCities({ commit, rootGetters }, tenantId) {
        const buildingInformationService = new fromService.BuildingInformationService(rootGetters['account/token'])
        buildingInformationService.getCities(tenantId).then(listOfCities => {
            commit('setCities', listOfCities)
        })
    },

    getBuildings({ commit, rootGetters }, locationId) {
        const buildingInformationService = new fromService.BuildingInformationService(rootGetters['account/token'])
        buildingInformationService.getBuildings(locationId).then(listOfBuildings => {
            commit('setBuildings', listOfBuildings)
        })
    },

    getBuildingsWithFloors({ commit, rootGetters }, locationId) {
        const buildingInformationService = new fromService.BuildingInformationService(rootGetters['account/token'])
        buildingInformationService.getBuildingsWithFloors(locationId).then(listOfBuildingsWithFloors => {
            commit('setBuildingsWithFloors', listOfBuildingsWithFloors)
        })
    },

    getFloors({ commit, rootGetters }, buildingId) {
        const buildingInformationService = new fromService.BuildingInformationService(rootGetters['account/token'])
        buildingInformationService.getFloors(buildingId).then(listOfFloors => {
            commit('setFloors', listOfFloors)
        })
    },

    postBuildingSetup({ dispatch, rootGetters }, buildingSetup) {
        const buildingInformationService = new fromService.BuildingInformationService(rootGetters['account/token'])
        return buildingInformationService.postBuildingSetup(buildingSetup).then(
            data => {
                if (data) {
                    dispatch('alert/success', 'Building setup successfully uploaded', { root: true })
                }
            },
            error => {
                dispatch('alert/error', error.response.data.Message, { root: true })
            }
        )
    }
}

const mutations = {
    setTenants: (state, listOfTenants) => {
        state.listOfTenants = listOfTenants
    },

    setCities: (state, listOfCities) => {
        state.listOfCities = listOfCities.sort((a, b) => (a.name > b.name ? 1 : -1))
    },

    setBuildings: (state, listOfBuildings) => {
        state.listOfBuildings = listOfBuildings.sort((a, b) => (a.name > b.name ? 1 : -1))
    },

    setBuildingsWithFloors: (state, listOfBuildingsWithFloors) => {
        if (listOfBuildingsWithFloors) {
            listOfBuildingsWithFloors.sort((a, b) => (a.label > b.label ? 1 : -1))
            listOfBuildingsWithFloors.forEach(building => {
                building.children.sort((a, b) => (a.level > b.level ? 1 : -1))
            })
        }

        state.listOfBuildingsWithFloors = listOfBuildingsWithFloors
    },

    setFloors: (state, listOfFloors) => {
        state.listOfFloors = listOfFloors.sort((a, b) => (a.level > b.level ? 1 : -1))
    }
}

const getters = {
    buildingInformationState: state => state.status
}

export const buildingInformation = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
