
const state = {
    newFloorId: undefined,
    assetSelected: undefined
}

const actions = {
    changeFloor({commit}, newFloorId) {
        commit('changeFloor', newFloorId);
    },
    selectAsset({commit}, assetId) {
        commit('selectAsset', assetId);
    },
    removeAsset({commit}) {
        commit('selectAsset', undefined);
    },
    changeFloorAndAsset({commit}, location) {
        commit('changeFloorAndAsset', location);
    }
}

const mutations = {
    changeFloor: (state, newFloorId) => {
        state.newFloorId = newFloorId
    },
    selectAsset: (state, assetId) => {
        state.assetSelected = assetId
    },
    changeFloorAndAsset: (state, location) => {
        state.newFloorId = location.floorId;
        state.assetSelected = location.deskId;
    }
}

export const mapInteractions = {
    namespaced: true,
    state,
    actions,
    mutations,
}