import * as fromServices from '@/services'

export const namespaced = true

export const state = {
    data: []
}

export const actions = {
    getBuildings({ rootGetters, commit }) {
        const token = rootGetters['account/token']
        const { locationId } = rootGetters['account/userLocation']
        const buildingService = new fromServices.BuildingInformationService(token)
        buildingService.getBuildings(locationId).then(data => commit('setBuildings', data))
    }
}

export const mutations = {
    setBuildings(state, data) {
        state.data = data
    }
}

export const getters = {
    listOfBuildings: state => state.data
}
