import Map from 'ol/Map.js'
import View from 'ol/View.js'
import ImageLayer from 'ol/layer/Image.js'
import Projection from 'ol/proj/Projection.js'
import Static from 'ol/source/ImageStatic.js'
import Group from 'ol/layer/Group.js'
import { getCenter } from 'ol/extent.js'
import { Draw, Modify, Snap } from 'ol/interaction.js'
import { Vector as VectorLayer } from 'ol/layer.js'
import { Vector as VectorSource } from 'ol/source.js'
import { Circle as CircleStyle, Fill, Stroke, Style, Icon } from 'ol/style.js'
import { defaults as defaultInteractions, DragRotateAndZoom, Select, Translate } from 'ol/interaction.js'
import Polygon from 'ol/geom/Polygon.js'
import GeoJSON from 'ol/format/GeoJSON.js'
import Feature from 'ol/Feature.js'
import Point from 'ol/geom/Point.js'
import Collection from 'ol/Collection'

export const mapService = {
    createMap,
    createVectorLayerWithFeatures,
    createVectorLayer,
    drawPolygon,
    modifyShape,
    snapOnShape,
    drawRectangle,
    translateShape,
    selectShape,
    getGeoJsonFromFeature,
    removeAllLayers,
    createImageLayer,
    translateOneLayer,
    addNewHazardIconAtCoordinates,
    addLayerWithHazards,
    createDeskLayer,
    searchFeatureOnFloorMap,
    getLayer,
    createMapSource
}

const viewExtent = [0, 0, 1024, 968]
const viewProjection = new Projection({
    code: 'xkcd-image',
    units: 'pixels',
    extent: viewExtent
})

function createMapSource(image) {
    return new Static({
        url: image,
        projection: viewProjection,
        imageExtent: viewExtent
    })
}

function getLayer(source) {
    return new ImageLayer({
        source: source,
        opacity: 0.6
    })
}

function createMap(mapLayer) {
    return new Map({
        interactions: defaultInteractions().extend([new DragRotateAndZoom()]),
        layers: [mapLayer],
        target: 'map',
        view: new View({
            zoom: 2.5,
            minZoom: 1.2,
            maxZoom: 4.5,
            projection: viewProjection,
            center: getCenter(viewExtent),
            extent: viewExtent
        })
    })
}

function createVectorLayerWithFeatures(assetSelected, listOfFeatures) {
    var fullColor = assetSelected.color.slice(0, 7)
    var source = new VectorSource({
        wrapX: false,
        format: new GeoJSON(),
        features: new GeoJSON().readFeatures(listOfFeatures)
    })

    var vector = new VectorLayer({
        source: source,
        style: new Style({
            fill: new Fill({
                color: assetSelected.color
            }),
            stroke: new Stroke({
                color: fullColor,
                width: 2
            }),
            image: new CircleStyle({
                radius: 7,
                fill: new Fill({
                    color: assetSelected.color
                })
            })
        }),
        name: assetSelected.name
    })
    return vector
}

function createVectorLayer(assetSelected) {
    var fullColor = assetSelected.color.slice(0, 7)
    var source = new VectorSource({
        wrapX: false,
        format: new GeoJSON()
    })

    var vector = new VectorLayer({
        source: source,
        style: new Style({
            fill: new Fill({
                color: assetSelected.color
            }),
            stroke: new Stroke({
                color: fullColor,
                width: 2
            }),
            image: new CircleStyle({
                radius: 7,
                fill: new Fill({
                    color: assetSelected.color
                })
            })
        }),
        name: assetSelected.name
    })
    return vector
}

function drawPolygon(layer, assetSelected) {
    var source = layer.getSource()
    var draw
    draw = new Draw({
        source: source,
        type: assetSelected.type
    })
    return draw
}

function modifyShape(layer) {
    var source = layer.getSource()
    var modify = new Modify({ source: source })
    return modify
}

function snapOnShape(layer) {
    var source = layer.getSource()
    var snap = new Snap({ source: source })
    return snap
}

function drawRectangle(layer, assetSelected) {
    var source = layer.getSource()
    var geometryFunction = function(coordinates, geometry) {
        var newCoordinates = []
        var lengthTranslation = assetSelected.length / 2
        var widthTranslation = assetSelected.width / 2
        newCoordinates.push([coordinates[0] + lengthTranslation, coordinates[1] - widthTranslation])
        newCoordinates.push([coordinates[0] + lengthTranslation, coordinates[1] + widthTranslation])
        newCoordinates.push([coordinates[0] - lengthTranslation, coordinates[1] + widthTranslation])
        newCoordinates.push([coordinates[0] - lengthTranslation, coordinates[1] - widthTranslation])
        newCoordinates.push([coordinates[0] + lengthTranslation, coordinates[1] - widthTranslation])
        geometry = new Polygon([newCoordinates])
        return geometry
    }
    var draw = new Draw({
        source: source,
        type: assetSelected.type,
        geometryFunction: geometryFunction
    })
    return draw
}
function selectShape() {
    var select = new Select()
    return select
}

function translateShape(select) {
    var translate = new Translate({
        features: select.getFeatures()
    })
    return translate
}

function getGeoJsonFromFeature(feature) {
    let parser = new GeoJSON()
    let shape = parser.writeFeatureObject(feature)
    // let shapeString = JSON.stringify(shape);
    return shape
}

function removeAllLayers(map) {
    map.setLayerGroup(new Group())
    return map
}

function createImageLayer(image) {
    var extent = [0, 0, 1024, 968]
    var projection = new Projection({
        code: 'xkcd-image',
        units: 'pixels',
        extent: extent
    })
    var imageLayer = new ImageLayer({
        source: new Static({
            url: image,
            projection: projection,
            imageExtent: extent
        }),
        opacity: 0.6
    })
    return imageLayer
}

function translateOneLayer(layer) {
    var features = layer.getSource().getFeatures()
    var featureCollection = new Collection()
    featureCollection.extend(features)
    var translate = new Translate({
        features: featureCollection
    })
    return translate
}

function addNewHazardIconAtCoordinates(coordinates) {
    var iconFeature = new Feature({
        geometry: new Point([coordinates[0], coordinates[1]])
    })

    var iconStyle = new Style({
        image: new Icon({
            anchor: [0.5, 46],
            anchorXUnits: 'fraction',
            anchorYUnits: 'pixels',
            src: require('../../svg-icons/pin2.svg')
        })
    })

    iconFeature.setStyle(iconStyle)

    var vectorSource = new VectorSource({
        features: [iconFeature]
    })

    var vectorLayer = new VectorLayer({
        source: vectorSource
    })

    return vectorLayer
}

function addLayerWithHazards(listOfFeatures) {
    var iconStyle = new Style({
        image: new Icon({
            anchor: [0.5, 46],
            anchorXUnits: 'fraction',
            anchorYUnits: 'pixels',
            src: require('../../svg-icons/pin.svg')
        })
    })

    var listOfConvertedFeatures = []
    listOfFeatures.features.forEach(feature => {
        var featureConverted = new GeoJSON().readFeature(feature)
        featureConverted.setStyle(iconStyle)
        listOfConvertedFeatures.push(featureConverted)
    })

    var source = new VectorSource({
        features: listOfConvertedFeatures
    })

    var vector = new VectorLayer({
        source: source,
        name: 'Hazard'
    })
    return vector
}

function createDeskLayer(desk, listOfFeatures) {
    var fullColorFreeDesk = desk.color.slice(0, 7)
    var fullColorOccupiedDesk = desk.colorOccupied.slice(0, 7)

    var styles = {
        false: new Style({
            fill: new Fill({
                color: desk.color
            }),
            stroke: new Stroke({
                color: fullColorFreeDesk,
                width: 2
            }),
            image: new CircleStyle({
                radius: 7,
                fill: new Fill({
                    color: desk.color
                })
            })
        }),
        true: new Style({
            fill: new Fill({
                color: desk.colorOccupied
            }),
            stroke: new Stroke({
                color: fullColorOccupiedDesk,
                width: 2
            }),
            image: new CircleStyle({
                radius: 7,
                fill: new Fill({
                    color: desk.colorOccupied
                })
            })
        })
    }

    var styleFunction = function(feature, resolution) {
        var featureStyleFunction = feature.getStyleFunction()
        if (featureStyleFunction) {
            return featureStyleFunction.call(feature, resolution)
        } else {
            return styles[feature.getProperties().Occupied]
        }
    }

    var source = new VectorSource({
        wrapX: false,
        format: new GeoJSON(),
        features: new GeoJSON().readFeatures(listOfFeatures)
    })

    var vector = new VectorLayer({
        source: source,
        name: desk.name,
        style: styleFunction
    })
    return vector
}

function searchFeatureOnFloorMap(map, featureId) {
    var searchedFeature
    map.getLayers()
        .getArray()
        .forEach(layer => {
            if (layer instanceof VectorLayer) {
                layer
                    .getSource()
                    .getFeatures()
                    .forEach(feature => {
                        var id = feature.getProperties().Id
                        if (id === featureId) {
                            searchedFeature = feature
                        }
                    })
            }
        })
    return searchedFeature
}
