import BaseService from './base-service'

export class EmployeeService extends BaseService {
    constructor(token) {
        super(token)
    }

    updateEmployeeFloor(employeeLocation) {
        return this.apiClient.post('/employees/location', JSON.stringify(employeeLocation))
    }

    getEmployees(floorArray) {
        let params = ''
        if (floorArray !== null) {
            params = floorArray.reduce((p, n) => `floorIds=${n}&`)
        }
        const url = `/employees/AllEmployeesByFloor?${params}`
        return this.apiClient.get(url)
    }

    getAllEmployeeInfo(userName) {
        const url = `/employees/InformationByUsername/${userName}`
        return this.apiClient.get(url)
    }

    getProfile() {
        return this.apiClient.get('/employees/profile')
    }
}
