import * as fromService from '@/services'

const state = {}

const actions = {
    postDesk({ dispatch, rootGetters }, desk) {
        const deskService = new fromService.DeskService(rootGetters['account/token'])

        deskService.postDesk(desk).then(
            data => {
                if (data) {
                    dispatch('alert/success', 'Desk successfully uploaded', { root: true })
                    dispatch('assetInteraction/setReloadMap', true, { root: true })
                }
            },
            error => {
                dispatch('alert/error', error.response.data.Message, { root: true })
                dispatch('assetInteraction/setReloadMap', true, { root: true })
            }
        )
    }
}
const mutations = {}

const getters = {
    deskState: state => state.status
}
export const desks = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
}
