<template>
    <v-card class="layer-selector-container">
        <v-layout column align-center fill-height>
            <v-flex xs12 class="title">
                <span>Layers</span>
            </v-flex>
            <v-flex xs12>
                <v-layout column>
                    <template v-for="(layer, key) in layers">
                        <v-flex xs10 :key="key">
                            <v-checkbox
                                v-model="layerSelectedComputed"
                                :value="layer.name"
                                :label="layer.name"
                                :color="layer.color.slice(0, 7)"
                            ></v-checkbox>
                        </v-flex>
                    </template>
                    <v-flex xs10>
                        <hr />
                    </v-flex>
                    <v-flex xs1>
                        <v-checkbox v-model="selectAllLayersComputed" label="Select all layers"></v-checkbox>
                    </v-flex>
                </v-layout>
            </v-flex>
        </v-layout>
    </v-card>
</template>

<script>
import { layers } from '@/store/commonSpaces.entityTypes.js'
import { mapActions, mapState } from 'vuex'
export default {
    data: () => ({
        layers: { ...layers }
    }),
    methods: {
        ...mapActions('employeeMap', ['setLayerSelected', 'setSelectAllLayers'])
    },
    computed: {
        ...mapState('employeeMap', ['layerSelected', 'selectAllLayers']),
        layerSelectedComputed: {
            get() {
                return this.layerSelected
            },
            set(newLayerSelected) {
                this.setLayerSelected(newLayerSelected)
            }
        },
        selectAllLayersComputed: {
            get() {
                return this.selectAllLayers
            },
            set(newSelectAllLayers) {
                this.setSelectAllLayers(newSelectAllLayers)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import 'src/stylesheets/1-setttings/settings.colors.scss';
@import 'src/styles/global.scss';

.title {
    padding-top: 20px;
    text-align: center;
    color: $orange;
}

.flex {
    max-height: 55px !important;
}

.layer-selector-container {
    height: 100%;
    min-width: 250px;
    background-color: white;
}
</style>
