import clientFactory from '@/common/apiClientFactory'

export class FloorLayoutService {
    constructor(token) {
        this.apiClient = clientFactory.apiClient(token)
    }

    uploadLayout(imageInformation) {
        return this.apiClient.post('/floors/layout', JSON.stringify(imageInformation))
    }

    getLayout(floorId) {
        return this.apiClient.get(`/floors/layout/${floorId}`)
    }
}
