
const state = {
    sliderHidden: false
}

const actions = {
    hideSlider({commit}) {
        commit('changeSlider', true)
    },
    showSlider({commit}) {
        commit('changeSlider', false)
    }
}

const mutations = {
    changeSlider: (state, sliderValue) => {
        state.sliderHidden = sliderValue
    }
}

export const navbar = {
    namespaced: true,
    state,
    actions,
    mutations,
}