import BaseService from './base-service'

export class BuildingInformationService extends BaseService {
    constructor(token) {
        super(token)
    }

    getTenants() {
        return this.apiClient.get('/tenants')
    }

    getCities(tenantId) {
        return this.apiClient.get(`/tenants/${tenantId}/locations`)
    }

    getBuildings(locationId) {
        return this.apiClient.get(`/locations/${locationId}/buildings`)
    }

    getBuildingsWithFloors(locationId) {
        return this.apiClient.get(`/locations/${locationId}/buildingsWithFloors`)
    }

    getFloors(buildingId) {
        return this.apiClient.get(`/buildings/${buildingId}/floors`)
    }

    postBuildingSetup(buildingSetup) {
        return this.apiClient.post('/buildings', JSON.stringify(buildingSetup))
    }
}
