const state = {
    showAdminToolbar: true
}

const actions = {
    hideAdminToolbar({ commit }) {
        commit('changeAdminToolbar', false)
    },
    showAdminToolbar({ commit }) {
        commit('changeAdminToolbar', true)
    }
}

const mutations = {
    changeAdminToolbar: (state, show) => {
        state.showAdminToolbar = show
    }
}

export const toolbar = {
    namespaced: true,
    state,
    actions,
    mutations
}
