import BaseService from './base-service'

export class PlacePeopleService extends BaseService {
    constructor(token) {
        super(token)
    }

    postDeskEmployee(deskId, employeeId) {
        return this.apiClient.post(
            '/DeskEmployee',
            JSON.stringify({
                DeskId: deskId,
                EmployeeId: employeeId
            })
        )
    }

    getAllEmployees() {
        return this.apiClient.get('/Search/employees')
    }
}
