import axios from 'axios'
import { apiConfig } from './apiConfig'
import store from '@/store'

const configFactory = () => ({
    timeout: 5 * 1000,
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    }
})

function handleError (error) {
    return handleResponse(error.response)
}

function handleResponse(response) {
    const data = response.data.data

    const success = response.status >= 200 && response.status < 300
    const defaultError = `Request failed with status ${response.status}`

    if (success) {
        return data;
    }

    if (response.status === 401) {
        store.dispatch('account/logout')
    }

    const error = (data && data.errors) || defaultError
    throw error
}

function createClient(token, baseUrl) {
    const config = configFactory()
    config.baseURL = baseUrl
    config.headers = {
        Authorization: `Bearer ${token}`,
        ...config.headers
    }

    const client = axios.create(config)
    client.interceptors.response.use(
        handleResponse,
        handleError
    );

    return client;
}

function apiClient(token) {
    return createClient(token, apiConfig.apiUrl)
}
function accountsClient(token) {
    return createClient(token, apiConfig.accountsUrl)
}

export default {
    apiClient,
    accountsClient
}