import BaseService from './base-service'

export class BathroomService extends BaseService {
    constructor(token) {
        super(token)
    }

    postBathroom(bathroom) {
        return this.apiClient.post('/bathrooms', JSON.stringify(bathroom))
    }

    getBathroomsForFloor(floorId) {
        return this.apClient(`/floors/${floorId}/bathrooms`)
    }
}
