<template>
    <v-container pa-0>
        <div id="map" class="inner-employee-map"></div>
    </v-container>
</template>

<script>
import { mapService } from '@/services/map.service.js'
import * as fromService from '@/services'

import { PRINTER, BATHROOM, meetingRoom, KITCHEN, DESK } from '@/store/commonSpaces.entityTypes.js'
import { mapState, mapActions } from 'vuex'
import store from '@/store'

export default {
    data: () => ({
        map: undefined,
        layersList: [],
        layer: undefined,
        select: undefined,
        hazardLayer: undefined,
        translateHazard: undefined
    }),
    mounted() {
        // create select interaction
        this.select = mapService.selectShape()
        this.select.on('select', e => {
            var selectedFeatureId = null
            if (e.target.getFeatures().getArray()[0]) {
                selectedFeatureId = e.target
                    .getFeatures()
                    .getArray()[0]
                    .getProperties().Id
            }
            this.getAssetDetails(selectedFeatureId)
        })
        // create the map

        const floorLayoutService = new fromService.FloorLayoutService(store.getters['account/token'])
        const floorId = store.getters['account/userLocation'].floorId
        floorLayoutService.getLayout(floorId).then(layout => {
            if (layout) {
                const source = mapService.createMapSource(layout.floorLayoutImage)
                const layer = mapService.getLayer(source)
                this.map = mapService.createMap(layer)
                // get common spaces
                this.getCommonSpacesLayers(floorId)
                this.map.addInteraction(this.select)
            }
        })
    },
    computed: {
        ...mapState('employeeMap', ['layerSelected', 'reloadAssets', 'reportHazard', 'hazardPosition']),
        ...mapState('mapInteractions', ['newFloorId', 'assetSelected'])
    },
    watch: {
        reportHazard() {
            if (this.reportHazard) {
                this.map.on('singleclick', e => {
                    if (!this.hazardLayer) {
                        // create and add a layer for the new hazard that contains a feature with an icon
                        this.hazardLayer = mapService.addNewHazardIconAtCoordinates(e.coordinate)
                        this.map.addLayer(this.hazardLayer)
                        // save the position in store as geojson
                        var hazardFeatureGeoJson = JSON.stringify(mapService.getGeoJsonFromFeature(this.hazardLayer.getSource().getFeatures()[0]))
                        this.setHazardPosition(hazardFeatureGeoJson)
                        // create and add to the map a translate interaction that applies only to the hazard layer
                        this.translateHazard = mapService.translateOneLayer(this.hazardLayer)
                        this.map.addInteraction(this.translateHazard)
                        // when the pin is moved we save the new location as a geojson
                        this.translateHazard.on('translateend', () => {
                            var hazardFeatureGeoJson = JSON.stringify(mapService.getGeoJsonFromFeature(this.hazardLayer.getSource().getFeatures()[0]))
                            this.setHazardPosition(hazardFeatureGeoJson)
                        })
                    }
                    this.map.removeEventListener('singleclick')
                })
            } else {
                this.removeNewHazard()
            }
        },
        layerSelected() {
            // remove all the layers on the map
            this.layersList.forEach(layer => {
                this.map.removeLayer(layer)
            })
            // add just the layers that are selected
            this.layerSelected.forEach(selectedLayerName => {
                var selectedLayer = this.layersList.find(layer => {
                    return layer.get('name') === selectedLayerName
                })
                if (selectedLayer) {
                    this.map.addLayer(selectedLayer)
                }
            })
        },
        reloadAssets() {
            if (this.reloadAssets) {
                this.reloadAssetsOnMap()
            }
        },
        hazardPosition() {
            if (!this.hazardPosition) {
                this.removeNewHazard()
            }
        },
        newFloorId() {
            // console.log('newFloorId watcher')

            //delete old map
            this.map.setTarget(null)

            //create map with new floorId
            this.getLayout(this.newFloorId).then(layout => {
                if (layout) {
                    // console.log('before create map')
                    const source = mapService.createMapSource(layout.floorLayoutImage)
                    const layer = mapService.getLayer(source)
                    this.map = mapService.createMap(layer)
                    // get common spaces
                    this.getNewFloorCommonSpacesLayers(this.newFloorId)
                    this.map.addInteraction(this.select)
                    // console.log('after create map')
                }
            })
        },
        assetSelected() {
            // console.log('assetSelected watcher')
            if (this.assetSelected !== undefined) {
                this.selectFeatureById(this.assetSelected)
            } else {
                this.select.getFeatures().clear()
            }
        }
    },
    methods: {
        ...mapActions('employeeMap', ['setReloadAssets', 'setHazardPosition']),
        ...mapActions('floorLayout', ['getLayout']),
        getCommonSpacesLayers(floorId) {
            const commonSpacesService = new fromService.CommonSpacesService(store.getters['account/token'])
            commonSpacesService.getCommonSpaces(floorId).then(commonSpaceDictionary => {
                for (var commonSpaceKey in commonSpaceDictionary) {
                    var featureCollection = JSON.parse(commonSpaceDictionary[commonSpaceKey])
                    this.addLayerWithFeatures(featureCollection, commonSpaceKey)
                }
            })
        },
        getNewFloorCommonSpacesLayers(newFloor) {
            const commonSpacesService = new fromService.CommonSpacesService(store.getters['account/token'])
            commonSpacesService.getCommonSpaces(newFloor).then(commonSpaceDictionary => {
                for (var commonSpaceKey in commonSpaceDictionary) {
                    var featureCollection = JSON.parse(commonSpaceDictionary[commonSpaceKey])
                    this.addLayerWithFeatures(featureCollection, commonSpaceKey)
                }
            })
        },
        addLayerWithFeatures(featureCollection, type) {
            // create layer based on feature type
            switch (type) {
                case 'printer':
                    this.layer = mapService.createVectorLayerWithFeatures(PRINTER, featureCollection)
                    break
                case 'bathroom':
                    this.layer = mapService.createVectorLayerWithFeatures(BATHROOM, featureCollection)
                    break
                case 'meetingroom':
                    this.layer = mapService.createVectorLayerWithFeatures(meetingRoom, featureCollection)
                    break
                case 'kitchen':
                    this.layer = mapService.createVectorLayerWithFeatures(KITCHEN, featureCollection)
                    break
                case 'desk':
                    this.layer = mapService.createDeskLayer(DESK, featureCollection)
                    break
                case 'hazard':
                    this.layer = mapService.addLayerWithHazards(featureCollection)
                    break
            }
            // add the layer to map
            this.map.addLayer(this.layer)
            // add the layer to layersList
            this.layersList.push(this.layer)
        },
        getAssetDetails(selectedFeatureId) {
            // eslint-disable-next-line
            console.log(selectedFeatureId)
        },
        reloadAssetsOnMap() {
            const floorId = store.getters['account/selectedFloorId']
            this.removeNewHazard()
            this.removeCommonSpacesLayers()
            this.getCommonSpacesLayers(floorId)
            this.setReloadAssets(false)
        },
        removeCommonSpacesLayers() {
            this.layersList.forEach(layer => {
                this.map.removeLayer(layer)
            })
        },
        removeNewHazard() {
            if (this.hazardLayer) {
                this.map.removeLayer(this.hazardLayer)
                this.hazardLayer = null
            }
            if (this.translateHazard) {
                this.map.removeInteraction(this.translateHazard)
                this.translateHazard = null
            }
            this.map.removeEventListener('singleclick')
        },
        selectFeatureById(featureId) {
            var searchedFeature = mapService.searchFeatureOnFloorMap(this.map, featureId)
            this.select.getFeatures().clear()
            this.select.getFeatures().push(searchedFeature)
        }
    }
}
</script>

<style lang="scss" scoped>
@import 'src/styles/global.scss';
.inner-employee-map {
    position: absolute;
    top: 0;
    left: 0;
    background-color: white;
    height: calc(100vh -#{$toolbar-height});
    width: 100vw;
}
.inner-employee-map .ol-zoom {
    display: none !important;
}
</style>
